
.con{
	background-image: url(/static/img/bg.7624055.png);
	position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
}
.logo{
    position: absolute;
    top: 50px;
    left: 10%;
}
.logo-img {
    width: 246px;
}
.logo-spa{
    display: inline-block;
    position: absolute;
    height: 38px;
    line-height: 38px;
    top: 15px;
    margin-left: 15px;
    padding-left: 15px;
    border-left: 1px solid #fff;
    color: #fff;
    font-size: 24px;
    width: 150px;
}
.right{
    width: 600px;
    height: 100vh;
    background: rgba(0,0,0,0.3);
    position: absolute;
    top: 0;
    right: 0;
}
.rightmain{
    width: 416px;
    max-height: 531px;
    min-height: 456px;
    position: relative;
    background: #ffffff;
    border-radius: 4px;
    margin: 20% auto 0;
}
.icon{
    width: 60px;
    height: 60px;
    position: absolute;
    top: 0;
    right: 0;
    cursor: pointer;
}
.main {
    width: calc(100% - 76px);
    min-height: 300px;
    padding: 22px 38px;
}
.tabHead {
    width: 100%;
    z-index: 10;
    overflow: hidden;
    font-size: 12px;
    border-bottom: 1px solid #DEDEDE;
}
.tabHead span {
    display: block;
    width: 80px;
    float: left;
    position: relative;
    z-index: 2;
    font-size: 18px;
    text-align: center;
    cursor: pointer;
    line-height: 20px;
    padding: 8px 0;
}
.login {
    position: relative;
}
.form {
    display: block;
    margin-top: 0em;
    -webkit-margin-after: 1em;
            margin-block-end: 1em;
}
.form {
    margin-top: 10px;
}
.m-account{
    border-radius: 5px;
    background: #fff;
}
.form .m-account .item{
    border-width: 1px;
    border-style: solid;
    border-color: #e8e8e8;
    border-radius: 4px;
    height: 48px;
    margin-top: 20px;
}
.prex-icon{
    position: absolute;
    top: 4px;
    left: 2px;
}
.uop_icon{
}
.m-account > div{
    position: relative;
    overflow: hidden;
}
.form .m-account > div > input{
    width: 70%;
    border: none;
    height: 48px;
    margin-left: 48px;
    outline: none;
}
#rememberMe {
    margin-left: 0;
    height: auto;
	margin-top: 10px;
}
.btn {
    margin-top: 30px;
}
.botton {
    margin-top: 30px;
}
.login-btn {
    width: 100%;
    height: 50px;
    background: #2368de;
    border-radius: 4px;
    display: block;
    color: #fff;
    text-align: center;
    line-height: 50px;
    font-size: 18px;
    text-decoration: none;
    letter-spacing: 12px;
}
.help {
    position: relative;
    clear: both;
    margin-top: 12px;
    height: 26px;
}
.fo-password {
    text-decoration: none;
    font-size: 14px;
    line-height: 26px;
    color: #73757E;
    float: left;
}
.activation-account {
    text-decoration: none;
    font-size: 14px;
    line-height: 26px;
    color: #0c4af9;
    float: right;
}
.footer {
    width: calc(100% - 64px);
    position: absolute;
    bottom: 0px;
    left: 0px;
    height: 58px;
    background: #F3F8FE;
    border-radius: 0 0 6px 6px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: Center;
        -ms-flex-align: Center;
            align-items: Center;
    padding: 0 32px;
}
.login-icon{
    width: 60px;
    float: left;
}
.login-icon img{
	width: 32px;
}
.wenzi{
    width: 416px;
    margin: 20px auto 0;
    font-size: 12px;
    color: #FFFFFF;
    line-height: 24px;
}
.lang_switch {
    width: 104px;
    top: 50px;
    border: 1px solid rgba(255, 255, 255, 0.5);
    background: rgba(12,74,249,0.30);
}
.t_switch {
    height: 28px;
    position: absolute;
    text-align: left;
    cursor: pointer;
    right: 10%;
    border-radius: 4px;
    font-size: 14px;
}
.switch_list {
    padding: 5px 14px;
    background: #ffffff;
    -webkit-box-shadow: 0 6px 6px -2px rgb(40 75 96 / 18%);
            box-shadow: 0 6px 6px -2px rgb(40 75 96 / 18%);
    border-radius: 4px;
}
.active {
    border-bottom: 1px solid #2B6CDB;
}
.tabHead span.active a {
    color: #2B6CDB;
}

